.boughtItemThumb{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    padding: 50px;
    gap: 20px;
    min-height: 400px;

    @media (min-width: 1200px) {
        justify-content: flex-start;

    }
}

.buyingHistoryTitle{
padding: 50px;
color: inherit;
}

.cardThumb{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    padding: 10px;
    gap: 10px;
    width: 300px;
    height: 550px;
    border-radius: 10px;
    box-shadow: 0px 0px 15px 0px rgba(0,0,0,0.75);

}