.cardsMainThumb{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    padding: 10px;
    gap: 50px;
    margin-top: 50px;
}

.cardThumb{
    position: relative;
    display: flex;
    flex-direction: column;    
    align-items: center;
    justify-content: center;
    padding: 10px;
    gap: 10px;
    width: 300px;
    height: 600px;
    border-radius: 10px;
    box-shadow: 0px 0px 15px 0px rgba(0,0,0,0.75);

    &:hover{
        transform: scale(1.1);
        transition: transform 0.3s ease;
    }

}


.cardTitle{
    text-align: center;
    color: brown;
}

.image{
    width: 100%;
    height: auto;
}

.favourite{
    position: absolute;
    top: 30px;
    right: 20px;
    width: 30px;
    cursor: pointer;
}

.cardButton{
    font-family: Arial, Helvetica, sans-serif;
    font-size: 20px;
    font-weight: 600;
    width: 200px;
    height: 50px;
    color: #fff;
    background-color: cornflowerblue;
    border: none;
    border-radius: 10px;
    cursor: pointer;

    &:hover{
        transform: scale(1.1);
        transition: transform 0.3s ease;
    }

    &:active{       
         transition: transform 0.3s ease;
        box-shadow: 0px 0px 15px 0px rgba(0,0,0,0.75);

    }
}

.cardButtonAdded{
    font-family: Arial, Helvetica, sans-serif;
    font-size: 20px;
    font-weight: 600;
    width: 200px;
    height: 50px;
    color: #fff;
    background-color: lightgreen;
    border: none;
    border-radius: 10px;
    

    &:hover{
        transform: scale(1.1);
        transition: transform 0.3s ease;
    }

    &:active{       
         transition: transform 0.3s ease;
        box-shadow: 0px 0px 15px 0px rgba(0,0,0,0.75);

    }
}